import React, { useState } from 'react'
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import Paper from '@material-ui/core/Paper';
import { FaCheckCircle } from "react-icons/fa";
import Dragable from './SideBarMenu/Dragable';
import Style from './SideBarMenu/SideBarStyle';
import { makeStyles } from "@material-ui/core/styles";
import { AiOutlineMinus, AiOutlineDash, AiOutlineSmallDash } from "react-icons/ai";
import { RiCheckboxBlankCircleFill } from "react-icons/ri";
import { HiOutlineMinusSm } from "react-icons/hi";
import drawStyle from './DrawStyle'


const useStyles = makeStyles(Style);

const CustomPopper = (props) => {
    const classes = useStyles();

    const [selectedIndex,setSelectedIndex] = useState(drawStyle.colorIndex)
    const [blueBorder, setblueBorder] = useState(drawStyle.dashIndex)
    const [blueBorder1, setblueBorder1] = useState(drawStyle.widthIndex)

    const lineBorders = [
    {icon: <RiCheckboxBlankCircleFill size="1em" id="0" />},
    {icon: <RiCheckboxBlankCircleFill size="1.2em" id="1" />},
    {icon: <RiCheckboxBlankCircleFill size="1.4em" id="2" />},
    {icon: <RiCheckboxBlankCircleFill size="1.6em" id="3" />},
    {icon: <RiCheckboxBlankCircleFill size="1.8em" id="4" />},
    ]
    const lineBorders1=[
    {icon: <AiOutlineMinus size="2.2em" id="0" />},
    {icon:<AiOutlineSmallDash size="2em" style={{marginBottom:'3px'}}  id="1"  />},
    {icon: <AiOutlineDash size="2em" style={{marginBottom:'3px'}}   id="2" />},
    {icon: <AiOutlineDash size="2em" style={{marginBottom:'3px'}}  id="3" />},
    ]
    const getSubIcons = (subMenuIcons, setOpenState) =>{
        return subMenuIcons?.length > 0 && subMenuIcons?.map((subMenu,index)=>{
            return (
                <>
               
                <span onClick={() => !subMenu.props.drag && handleChange(index, subMenu.props.color)}>
                   {dragDrop(subMenu)}
                </span>
                </>
            )
        })
    }
    const handleChange = (index, color) =>{
        setSelectedIndex(index)
        drawStyle.setColor(index,color);
    }
    const handleBorderChange = (index, iconsId) => {
        setblueBorder(iconsId)
        drawStyle.setDashIndex(index);
    }
    const handleBorderChange1 = (index, iconsId) => {
        setblueBorder1(iconsId)
        drawStyle.setWidthIndex(index);
    }
    const dragDrop = (subMenu) =>{
        
        if(!subMenu.length)
        {
            if(selectedIndex != subMenu.props.id) {
                if(!subMenu.props.drag)
                {
                    return subMenu;
                }
                else{
                    return ( 
                        <Dragable subMenu={subMenu}>
                            <span>{subMenu }</span>
                        </Dragable>
                    )
                }
            } 
            else{
                if(!subMenu.props.drag) 
                    return <FaCheckCircle size="2em" color={subMenu.props.color}/>
            }
        }
    }

    const lineWidth = () => {
                      return ( 
                          <div className={classes.spaceBwtRound}>
                        {
                           lineBorders1?.length > 0 && lineBorders1.map((lineBorder, index) =>{
                                return(
                                    <span key={index} className={`${lineBorder.icon.props.id === blueBorder ? "changeBorder" : ""} `} onClick={() => handleBorderChange(index, lineBorder.icon.props.id) }>
                                        {lineBorder.icon}
                                    </span>
                                )
                            })
                        }
                        </div>
                      )
    }

    const lineWidth1 = () => {
        return ( 
            <div className={classes.spaceBwt} >
          {
             lineBorders?.length > 0 && lineBorders.map((lineBorder, index) =>{
                  return(
                      <span key={index} className={`${lineBorder.icon.props.id === blueBorder1 ? "changeBorderRound" : "defaultBorder"} `} onClick={() => handleBorderChange1(index, lineBorder.icon.props.id) }>
                          {lineBorder.icon}
                      </span>
                  )
              })
          }
          </div>
        )
}
    const {open, anchorEl, placement, subMenuIcons, setOpenState, close, selectedItemId} = props;
    return (
        <div>
            <Popper open={open} anchorEl={anchorEl} placement={placement} transition
            >
                {({ TransitionProps }) => (
                    <Fade {...TransitionProps} timeout={350}>
                        <Paper className={`${props?.subMenuIcons?.length === 15 ?  "popper_paper" : "smart_draw_paper"} ${props?.subMenuIcons?.length === 15 ?  "poper_paper_res" : "smart_draw_res"}`}>
                        {(selectedItemId == 3 || selectedItemId == 4) && lineWidth1()}
                        {(selectedItemId == 3 || selectedItemId == 4) && lineWidth()}

                            {getSubIcons(subMenuIcons)}
                         </Paper>
                     </Fade>
                )}
            </Popper>
        </div>
    )
}

export default CustomPopper;
