import $ from 'jquery'

var util = { };

util.setCanvasSize = function() {
    var dpr = window.devicePixelRatio || 1;
    var fx = function (cnv, width, height) {
      cnv.width = width * dpr;
      cnv.height = height * dpr;
      cnv.style.width = width + "px";
      cnv.style.height = height + "px";
      var ctx = cnv.getContext("2d");
      ctx.scale(dpr, dpr);
    }
  
    var vport = document.getElementById("viewport");
    var rect = vport.getBoundingClientRect();
    var panel = document.getElementById("gridCanvas");
    fx(panel, rect.width, rect.height);
    var canvas = document.getElementById("drawCanvas");
    fx(canvas, rect.width, rect.height);
}

util.drawGrid = function(panel) {
    var wd = panel.width;
    var ht = panel.height;
    var ctx = panel.getContext('2d');
    var step = 16;
    ctx.lineWidth = 0.1;
    ctx.beginPath();
    for (var i = 0; i < ht; i+= step) {
      ctx.moveTo(0, i);
      ctx.lineTo(wd, i);
    }
    for (i = 0; i < wd; i+= step) {
      ctx.moveTo(i, 0);
      ctx.lineTo(i, ht);
    }
    ctx.stroke();
}
  
util.drawCanvas = function() {
    var canvas = document.getElementById("drawCanvas");
    var ctx = canvas.getContext("2d");
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    window.Module.drawCanvas(ctx);
}

// Adding into window as this is being used by c++ code
window.generateUUID = function() {
    var d = new Date().getTime();//Timestamp
    var d2 = (window.performance && window.performance.now && (window.performance.now()*1000)) || 0;//Time in microseconds since page-load or 0 if unsupported
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random() * 16;//random number between 0 and 16
        if(d > 0){//Use timestamp until depleted
            r = (d + r)%16 | 0;
            d = Math.floor(d/16);
        } else {//Use microseconds since page-load if supported
            r = (d2 + r)%16 | 0;
            d2 = Math.floor(d2/16);
        }
        return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
}

async function loadTemplate(name) {
    await fetch(name)
      .then(response => response.json())
      .then(response => {
        window.Module.libraryStore().initObjects(JSON.stringify(response));
      })
}
  
async function loadAll() {
    await loadTemplate("templates/v4/templates.json")
    await loadTemplate("templates/v4/basic.json")
    await loadTemplate("templates/v4/arrow.json")
    await loadTemplate("templates/v4/callout.json")
    await loadTemplate("templates/v4/flowchart.json")
    await loadTemplate("templates/v4/connection_arrows.json")
}
  
util.loadBasicTemplates = function(columns) {
    loadAll()
      .then(function () {
        window.Module.libraryStore().init(columns);
        window.Module.libraryStore().onDisplaySettingChange();
        //collectAndPopulateShapeLibrary(columns);
      });
}
  
async function collectAndPopulateShapeLibrary(columns) {
    const shapeLibrary = await collectShapeLibrary(columns);
    populateShapeLibrary(shapeLibrary, columns);
    drawShapes(shapeLibrary, columns);
}
  
async function collectShapeLibrary(columns) {
    var cc = window.Module.libraryStore().categoryCount();
    const libraryDetails = {};
    for (var i = 0; i < cc; i++) {
      var name = window.Module.getCategoryName(i);
      if (!libraryDetails[name]) {
        libraryDetails[name] = [];
      }
      var rc = window.Module.libraryStore().rowCount(i);
      for (var j = 0; j < rc; j++) {
        for (var k = 0; k < columns; k++) {
          if (window.Module.libraryStore().hasObject(i, j, k)) {
            var nm = window.Module.getObjectName(i, j, k);
            libraryDetails[name].push({
              category: i,
              row: j,
              col: k,
              nm
            })
            // console.log(nm);
          }
        }
      }
    }
    return libraryDetails;
}
  
function populateShapeLibrary(shapeLib, columns) {
    let colKeys = Object.keys(shapeLib);
    for (let colKey of colKeys) {
      const colData = shapeLib[colKey];
      let d = `<li class="nav-item left-pane-li">
      <a class="nav-link" href="#">${colKey}</a>
      <div id="stdiv-${colKey}"></div></li>`;
      $("#shapeLibUl").append(d);
    }
}
  
function drawShapes(shapeLib, columns) {
    let colKeys = Object.keys(shapeLib);
    for (let colKey of colKeys) {
      const colData = shapeLib[colKey];
      const divEl = document.getElementById(`stdiv-${colKey}`);
      for (let data of colData) {
        const id = `st${data.category}${data.row}${data.col}`;
        var canv = document.createElement("canvas");
        canv.setAttribute('width', 50);
        canv.setAttribute('height', 50);
        canv.setAttribute('id', id);
        canv.setAttribute('data-category', data.category);
        canv.setAttribute('data-row', data.row);
        canv.setAttribute('data-col', data.col);
        canv.classList.add('draggable');
        canv.drop = function(x, y) {
          var objNum = columns * data.row + data.col;
          window.Module.libraryStore().addObjectToCanvas(data.category, objNum, x, y);
          util.drawCanvas();
        }
        divEl.appendChild(canv);
        //
        const panel = canv;
        const ctx = panel.getContext('2d');
        window.Module.drawCell(data.category, data.row, data.col, 35, 25, 25, ctx);
  
        $(canv).draggable({
          helper: "clone"
        });
      }
    }
}
  
export default util;
  