import React, { useState } from 'react';
import { AppBar, Grid, Toolbar, IconButton, Menu, MenuItem } from "@material-ui/core";
import MenuIcon from '@material-ui/icons/Menu';
import Style from './HeaderStyle';
import { makeStyles } from "@material-ui/core/styles";
import NestedMenuItem from "material-ui-nested-menu-item";
import CheckIcon from '@material-ui/icons/Check';
import TextEditor from '../SideBarMenu/TextEditor';
import ClickAwayListener from '@material-ui/core/ClickAwayListener'

const useStyles = makeStyles(Style);
const AppMenu = () => {
    const classes = useStyles();
    const [value, setValue] = useState('');
    const [open, setOpen] = useState(false);
    const [openPoper, setOpenPoper] = useState(false);
    const [id, setId] = useState('');
    const [boolVal, setBoolVal] = useState(false)

    const options = [
        { id: 1, name: 'Insert diagram' },
        { id: 2, name: 'Default font' },
        { id: 3, name: 'Snap to grid' },
        { id: 4, name: 'Help'},
    ];

    const subOptions = [
        { id: 1, name: 'PDF' },
        { id: 2, name: 'PNG' },
    ]

    const handleClick = (e) => {
        setValue(e.currentTarget)
        setOpen(true)
    }

    const PoperOpen = (e) => {
        setOpenPoper(openPoper ? false : e.currentTarget)
    }
    const handleMenuClick = (id) => {
        setBoolVal(id === 3 ? !boolVal : false)

        setId(id)
        setOpen(false)
    }

    const DialogClose = () => {
        setId('');
    }
    return (
        <Grid container>
            <AppBar className="HeaderSection" position='static' color='transparent' style={{ background: 'transparent', boxShadow: 'none' }}>
                <Toolbar>
                    <IconButton className="HeaderMenuIcon" color="inherit" aria-label="menu" onClick={(e) => handleClick(e)}>
                        <MenuIcon />
                    </IconButton>
                    <Menu
                        onClose={() => setOpen(false)}
                        open={open}
                        value={value}
                        PaperProps={{
                            style: {
                                width: 200,
                                marginTop: 40
                            },
                        }}
                    >
                        <NestedMenuItem
                            style={{ left: '15px' }}
                            label="Export"
                            onClick={() => handleMenuClick('exp')}
                            parentMenuOpen={open}
                            className="subIcon"
                        >
                            {
                                subOptions.map((subOption, index) => (
                                    <MenuItem key={index}>{subOption.name}</MenuItem>
                                ))}
                        </NestedMenuItem>
                        {options?.map((option, index) => {
                            return (
                                <div key={index}>
                                    { index === 2 && boolVal && <span className={classes.checkIcon}><CheckIcon /></span> }
                                    <MenuItem style={{ left: '15px' }} value={option.name} onClick={() => handleMenuClick(option.id)} key={index}>
                                        {option.name}
                                    </MenuItem>
                                </div>
                            )
                        })}
                    </Menu>
                </Toolbar>
            </AppBar>
            { id === 2 ?
                <ClickAwayListener onClickAway={() => DialogClose()}>
                 <div className="text-editor">
                   <TextEditor /> 
                   </div>
                   </ClickAwayListener>
                : null}
        </Grid>
    )
}

export default AppMenu
